import React, { useEffect, useState } from 'react';

import Recipes from './components/Recipes';
import './App.css';

const App = () => {

  const [recipes, setRecipes] = useState([]);

  const [search, setSearch] = useState("");

  const [query, setQuery] = useState("chhole");

  useEffect(() => {
    getRecipe();
  }, [query]);

  const getRecipe = async () => {

    const API_ID = "009747f9&app_key=0e01c3c256a0d3b5c06708655ce3e5df";

    const response = await fetch(`https://api.edamam.com/api/recipes/v2?type=public&q=${query}&app_id=${API_ID}`);

    const data = await response.json();

    setRecipes(data.hits);
  }

  const handleChange = (e) => {
    const {value} = e.target;
    
    setSearch(value);
  }

  const handleClick = (e) => {
    e.preventDefault();
    setQuery(search);

    setSearch("");
  }

  return(
    <div className="App">

      <form onSubmit={handleClick}>
        <input onChange={handleChange} />

        <button>Search</button>
      </form>

      {recipes.map((recipe, index) => {
        return <Recipes key={index} recipes={recipe} />
      })}
    </div>
  );
}

export default App;