import React from 'react';

const Recipes = ({recipes}) => {

    console.log(recipes);

    return (
        <>
            <div className="app-image">
                <img src={recipes.recipe.image} alt={recipes.recipe.label} />

                <h3>{recipes.recipe.label}</h3>

                <h4>Calories: { Math.round(recipes.recipe.calories) / 1000} kcal</h4>
            </div>
        </>
    );
}

export default Recipes;